function parseDateTimeTZtoDate(timetz){
    let format = new Date(timetz);
    return `${format.getDate()}/${format.getMonth()}`;
}

function parseDateTimeTZtoFullDate(timetz){
    let format = new Date(timetz);
    return `${format.getDate()}-${format.getMonth()}-${format.getFullYear()}`;
}

module.exports = {
    parseDateTimeTZtoDate,
    parseDateTimeTZtoFullDate
}