import React, { useEffect, useState } from 'react';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import CheckIcon from '@mui/icons-material/Check';

import tanggalHelper from './../../../util/tanggalHelper';

export default function DataRegister(props) {
    
    const encryptedId = props.encryptedId;
    const pasien = props.pasien;
    useEffect(() => {
        setDataPasien(pasien?.studi_kasus_biodata[0]);
        const t_rekam_medik = pasien?.studi_kasus_rekam_medik[0];
        if (t_rekam_medik) {
            let t = t_rekam_medik.riwayat_obat.split("|");
            const t_el = t.map(function (v, i) {
                return (<li key={i}>{v}</li>);
            })
            t_rekam_medik.riwayat_obat_el = (<ul>{t_el}</ul>);
        }
        setDataRekamMedik(t_rekam_medik);
        setDataTandaVital(pasien?.studi_kasus_tanda_vital);
        setDataKlinis(pasien?.studi_kasus_klinis);
        setDataLaboratorium(pasien?.studi_kasus_laboratorium);
        setDataTerapi(pasien?.studi_kasus_terapi);
    }, [])

    const [dataPasien, setDataPasien] = useState({});
    const [dataRekamMedik, setDataRekamMedik] = useState({});
    const [dataTandaVital, setDataTandaVital] = useState([]);
    const [dataKlinis, setDataKlinis] = useState([]);
    const [dataLaboratorium, setDataLaboratorium] = useState([]);
    const [dataTerapi, setDataTerapi] = useState([]);

    return (
        <div>
            <Accordion defaultExpanded={true}>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography variant="h6">Data Pasien</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <table className="table-auto text-gray-700 border-collapse" width={'100%'}>
                        <tbody>
                            <tr className='border-b-2 border-slate-300'>
                                <td className='' width={'120px'}>Nama</td>
                                <td width={'10px'}>:</td>
                                <td className='font-semibold'>{dataPasien.nama}</td>
                            </tr>
                            <tr className='border-b-2 border-slate-300'>
                                <td className='pt-2' width={'120px'}>Usia</td>
                                <td className='pt-2' width={'10px'}>:</td>
                                <td className='pt-2 font-semibold'>{dataPasien.usia} Tahun</td>
                            </tr>
                            <tr className='border-b-2 border-slate-300'>
                                <td className='pt-2' width={'120px'}>BB/TB</td>
                                <td className='pt-2' width={'10px'}>:</td>
                                <td className='pt-2 font-semibold'>{dataPasien.berat_badan} kg / {dataPasien.tinggi_badan} cm</td>
                            </tr>
                            <tr className='border-b-2 border-slate-300'>
                                <td className='pt-2' width={'120px'}>Alamat</td>
                                <td className='pt-2' width={'10px'}>:</td>
                                <td className='pt-2 font-semibold'>{dataPasien.alamat}</td>
                            </tr>
                        </tbody>
                    </table>
                </AccordionDetails>
            </Accordion>
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel2a-content"
                    id="panel2a-header"
                >
                    <Typography variant='h6'>Rekam Medik</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <table className="text-gray-700 text-base table-fixed border-collapse border-spacing-y-2">
                        <tbody>
                            <tr className='border-b-2 border-slate-300'>
                                <td className='pt-2' width={'120px'}>No Rekam Medis</td>
                                <td className='pt-2' width={'10px'}>:</td>
                                <td className='pt-2 font-semibold'>{dataRekamMedik.no_rekam_medik}</td>
                            </tr>
                            <tr className='border-b-2 border-slate-300'>
                                <td className='pt-2' width={'120px'}>Tanggal MRS</td>
                                <td className='pt-2' width={'10px'}>:</td>
                                <td className='pt-2 font-semibold'>{tanggalHelper.parseDateTimeTZtoFullDate(dataRekamMedik.tanggal_mrs)}</td>
                            </tr>
                            <tr className='border-b-2 border-slate-300'>
                                <td className='pt-2' width={'120px'}>Tanggal KRS</td>
                                <td className='pt-2' width={'10px'}>:</td>
                                <td className='pt-2 font-semibold'>{tanggalHelper.parseDateTimeTZtoFullDate(dataRekamMedik.tanggal_krs)}</td>
                            </tr>
                            <tr className='border-b-2 border-slate-300'>
                                <td className='pt-2' width={'120px'}>Riwayat Penyakit</td>
                                <td className='pt-2' width={'10px'}>:</td>
                                <td className='pt-2 font-semibold'>{dataRekamMedik.riwayat_penyakit}</td>
                            </tr>
                            <tr className='border-b-2 border-slate-300'>
                                <td className='pt-2' width={'120px'}>Keluhan</td>
                                <td className='pt-2' width={'10px'}>:</td>
                                <td className='pt-2 font-semibold'>{dataRekamMedik.keluhan}</td>
                            </tr>
                            <tr className='border-b-2 border-slate-300'>
                                <td className='pt-2' width={'120px'}>Alergi</td>
                                <td className='pt-2' width={'10px'}>:</td>
                                <td className='pt-2 font-semibold'>{dataRekamMedik.alergi}</td>
                            </tr>
                            <tr className='border-b-2 border-slate-300'>
                                <td className='pt-2' width={'120px'}>Riwayat Obat</td>
                                <td className='pt-2' width={'10px'}>:</td>
                                <td className='pt-2 font-semibold'>{dataRekamMedik.riwayat_obat_el}</td>
                            </tr>
                            <tr className='border-b-2 border-slate-300'>
                                <td className='pt-2' width={'120px'}>Riwayat Keluarga</td>
                                <td className='pt-2' width={'10px'}>:</td>
                                <td className='pt-2 font-semibold'>{dataRekamMedik.riwayat_keluarga}</td>
                            </tr>
                            <tr className='border-b-2 border-slate-300'>
                                <td className='pt-2' width={'120px'}>Diagnosa</td>
                                <td className='pt-2' width={'10px'}>:</td>
                                <td className='pt-2 font-semibold'>{dataRekamMedik.diagnosa}</td>
                            </tr>
                        </tbody>
                    </table>
                </AccordionDetails>
            </Accordion>
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel3a-content"
                    id="panel3a-header"
                >
                    <Typography variant='h6'>Tanda Vital</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <div className='overflow-hidden max-w-[436px]'>
                        <div className='w-full overflow-auto'>
                            <table className="text-gray-700 text-base table-auto border-collapse border-spacing-y-2">
                                <tbody>
                                    {dataTandaVital && dataTandaVital.map(function (v, i) {
                                        if (i == 0) {
                                            let append_td = [];
                                            v.studi_kasus_tanda_vital_history.forEach((vh) => {
                                                let tanggal = tanggalHelper.parseDateTimeTZtoDate(vh.tanggal);
                                                append_td.push(<th className={'whitespace-nowrap text-center px-5 pt-2 border-slate-300 border'}>{tanggal}</th>)
                                            })

                                            return (
                                                <tr className='border-b-2 border-slate-300' key={i}>
                                                    <th className='sticky left-0 bg-slate-200 px-5 pt-2 border-slate-300 border'>Parameter</th>
                                                    <th className='whitespace-nowrap bg-slate-200 text-center px-5 pt-2 border-slate-300 border'>Normal</th>
                                                    {append_td}
                                                </tr>
                                            );
                                        }
                                    })}
                                    {dataTandaVital && dataTandaVital.map(function (v, i) {
                                        let stat_normal = `${v.batas_bawah}-${v.batas_atas}`;
                                        if (i == 0) {
                                            stat_normal = `${v.batas_atas}/${v.batas_bawah}`;
                                        }

                                        let append_td = [];
                                        v.studi_kasus_tanda_vital_history.forEach((vh, ih) => {
                                            let stat_normal_h = `${vh.nilai_bawah}-${vh.nilai_atas}`;
                                            if (i == 0) {
                                                stat_normal_h = `${vh.nilai_atas}/${vh.nilai_bawah}`;
                                            } else if (vh.nilai_atas == vh.nilai_bawah) {
                                                stat_normal_h = `${vh.nilai_atas}`;
                                            }
                                            let class_stat_h = '';
                                            if (vh.nilai_atas > v.batas_atas || vh.nilai_bawah < v.batas_bawah) {
                                                // class_stat_h = ' bg-red-400 text-white';
                                            }
                                            append_td.push(<td className={'whitespace-nowrap text-center px-5 pt-2 border-slate-300 border' + class_stat_h}>{stat_normal_h}</td>)
                                        })

                                        return (
                                            <tr className='border-b-2 border-slate-300'>
                                                <td className='sticky left-0 bg-slate-100 px-5 pt-2 border-slate-300 border'>{v.parameter}</td>
                                                <td className='whitespace-nowrap bg-slate-100 text-center px-5 pt-2 border-slate-300 border'>{stat_normal}</td>
                                                {append_td}
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </AccordionDetails>
            </Accordion>
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel4a-content"
                    id="panel4a-header"
                >
                    <Typography variant='h6'>Data Klinis</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <div className='overflow-hidden max-w-[436px]'>
                        <div className='w-full overflow-auto'>
                            <table className="text-gray-700 text-base table-auto border-collapse border-spacing-y-2">
                                <tbody>
                                    {dataKlinis && dataKlinis.map(function (v, i) {
                                        if (i == 0) {
                                            let append_td = [];
                                            v.studi_kasus_klinis_history.forEach((vh) => {
                                                let tanggal = tanggalHelper.parseDateTimeTZtoDate(vh.tanggal);
                                                append_td.push(<th className={'whitespace-nowrap text-center px-5 pt-2 border-slate-300 border'}>{tanggal}</th>)
                                            })

                                            return (
                                                <tr className='border-b-2 border-slate-300'>
                                                    <th className='sticky left-0 bg-slate-200 px-5 pt-2 border-slate-300 border'>Kondisi Klinik</th>
                                                    <th className='whitespace-nowrap bg-slate-200 text-center px-5 pt-2 border-slate-300 border'>Normal</th>
                                                    {append_td}
                                                </tr>
                                            );
                                        }
                                    })}
                                    {dataKlinis && dataKlinis.map(function (v, i) {
                                        let stat_normal = `${v.indikator_normal}`;

                                        let append_td = [];
                                        v.studi_kasus_klinis_history.forEach((vh, ih) => {
                                            let stat_normal_h = `${vh.nilai}`;
                                            let class_stat_h = '';
                                            if (vh.nilai == "+") {
                                                class_stat_h = ' bg-green-600 text-white';
                                            } else if (vh.nilai == "++") {
                                                class_stat_h = ' bg-red-600 text-white';
                                            }
                                            append_td.push(<td className={'whitespace-nowrap text-center px-5 pt-2 border-slate-300 border' + class_stat_h}>{stat_normal_h}</td>)
                                        })

                                        return (
                                            <tr className='border-b-2 border-slate-300'>
                                                <td className='sticky left-0 bg-slate-100 px-5 pt-2 border-slate-300 border'>{v.parameter}</td>
                                                <td className='whitespace-nowrap bg-slate-100 text-center px-5 pt-2 border-slate-300 border'>{stat_normal}</td>
                                                {append_td}
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </AccordionDetails>
            </Accordion>
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel5a-content"
                    id="panel5a-header"
                >
                    <Typography variant='h6'>Data Laboratorium</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <div className='overflow-hidden max-w-[436px]'>
                        <div className='w-full overflow-auto'>
                            <table className="text-gray-700 text-base table-auto border-collapse border-spacing-y-2">
                                <tbody>
                                    {dataLaboratorium && dataLaboratorium.map(function (v, i) {
                                        if (i == 0) {
                                            let append_td = [];
                                            v.studi_kasus_laboratorium_history.forEach((vh) => {
                                                let tanggal = tanggalHelper.parseDateTimeTZtoDate(vh.tanggal);
                                                append_td.push(<th className={'whitespace-nowrap text-center px-5 pt-2 border-slate-300 border'}>{tanggal}</th>)
                                            })

                                            return (
                                                <tr className='border-b-2 border-slate-300'>
                                                    <th className='sticky left-0 bg-slate-200 px-5 pt-2 border-slate-300 border'>Parameter</th>
                                                    <th className='whitespace-nowrap bg-slate-200 text-center px-5 pt-2 border-slate-300 border'>Normal</th>
                                                    {append_td}
                                                </tr>
                                            );
                                        }
                                    })}
                                    {dataLaboratorium && dataLaboratorium.map(function (v, i) {
                                        let stat_normal = `${v.batas_bawah}-${v.batas_atas}`;
                                        if (i == 0) {
                                            stat_normal = `${v.batas_atas}/${v.batas_bawah}`;
                                        }

                                        let append_td = [];
                                        v.studi_kasus_laboratorium_history.forEach((vh, ih) => {
                                            let stat_normal_h = `${vh.nilai_bawah}-${vh.nilai_atas}`;
                                            if (i == 0) {
                                                stat_normal_h = `${vh.nilai_atas}/${vh.nilai_bawah}`;
                                            } else if (vh.nilai_atas == vh.nilai_bawah) {
                                                stat_normal_h = `${vh.nilai_atas}`;
                                            }
                                            if (stat_normal_h == "null") {
                                                stat_normal_h = '-';
                                            }
                                            let class_stat_h = '';
                                            if (vh.nilai_atas > v.batas_atas || vh.nilai_bawah < v.batas_bawah) {
                                                // class_stat_h = ' bg-red-400 text-white';
                                            }
                                            append_td.push(<td className={'whitespace-nowrap text-center px-5 pt-2 border-slate-300 border' + class_stat_h}>{stat_normal_h}</td>)
                                        })

                                        return (
                                            <tr className='border-b-2 border-slate-300'>
                                                <td className='sticky left-0 bg-slate-100 px-5 pt-2 border-slate-300 border'>{v.parameter}</td>
                                                <td className='whitespace-nowrap bg-slate-100 text-center px-5 pt-2 border-slate-300 border'>{stat_normal}</td>
                                                {append_td}
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </AccordionDetails>
            </Accordion>
            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel6a-content"
                    id="panel6a-header"
                >
                    <Typography variant='h6'>Data Terapi</Typography>
                </AccordionSummary>
                <AccordionDetails>
                    <div className='overflow-hidden max-w-[436px]'>
                        <div className='w-full overflow-auto'>
                            <table className="text-gray-700 text-base table-auto border-collapse border-spacing-y-2">
                                <tbody>
                                    {dataTerapi && dataTerapi.map(function (v, i) {
                                        if (i == 0) {
                                            let append_td = [];
                                            v.studi_kasus_terapi_history.forEach((vh) => {
                                                let tanggal = tanggalHelper.parseDateTimeTZtoDate(vh.tanggal);
                                                append_td.push(<th className={'whitespace-nowrap text-center px-5 pt-2 border-slate-300 border'}>{tanggal}</th>)
                                            })

                                            return (
                                                <tr className='border-b-2 border-slate-300'>
                                                    <th className='sticky left-0 bg-slate-200 px-5 pt-2 border-slate-300 border'>Nama Obat</th>
                                                    <th className='whitespace-nowrap bg-slate-200 text-center px-5 pt-2 border-slate-300 border'>Dosis</th>
                                                    <th className='whitespace-nowrap bg-slate-200 text-center px-5 pt-2 border-slate-300 border'>Rute</th>
                                                    {append_td}
                                                </tr>
                                            );
                                        }
                                    })}
                                    {dataTerapi && dataTerapi.map(function (v, i) {
                                        let stat_normal = `${v.dosis}`;

                                        let append_td = [];
                                        v.studi_kasus_terapi_history.forEach((vh, ih) => {
                                            let stat_normal_h = `${vh.nilai}`;
                                            
                                            if (stat_normal_h == "null") {
                                                stat_normal_h = '-';
                                            } else {
                                                stat_normal_h = <CheckIcon />;
                                            }
                                            let class_stat_h = '';

                                            append_td.push(<td className={'whitespace-nowrap text-center px-5 pt-2 border-slate-300 border' + class_stat_h}>{stat_normal_h}</td>)
                                        })

                                        return (
                                            <tr className='border-b-2 border-slate-300'>
                                                <td className='sticky left-0 bg-slate-100 px-5 pt-2 border-slate-300 border'>{v.nama_obat}</td>
                                                <td className='whitespace-nowrap bg-slate-100 text-center px-5 pt-2 border-slate-300 border'>{stat_normal}</td>
                                                <td className='whitespace-nowrap bg-slate-100 text-center px-5 pt-2 border-slate-300 border'>{v.rute}</td>
                                                {append_td}
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </AccordionDetails>
            </Accordion>
        </div>
    );
}
